/**
 * External dependencies
 */
// import classnames from 'classnames';
import { dropRight, times } from "lodash";

/**
 * WordPress dependencies
 */
const { useEffect, useState } = wp.element;
import { __ } from "@wordpress/i18n";
import {
  PanelBody,
  RangeControl,
  ToggleControl,
  SelectControl,
  RadioControl,
  __experimentalNumberControl as NumberControl
} from "@wordpress/components";

import {
  InspectorControls,
  useInnerBlocksProps,
  __experimentalBlockVariationPicker,
  useBlockProps,
  InnerBlocks,
  RichText,
  store as blockEditorStore,
} from "@wordpress/block-editor";
import { withDispatch, useSelect, select } from "@wordpress/data";

import {
  createBlock,
  createBlocksFromInnerBlocksTemplate,
  store as blocksStore,
} from "@wordpress/blocks";


/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import "./editor.scss";

import $ from 'jquery';
/**
 * Allowed blocks constant is passed to InnerBlocks precisely as specified here.
 * The contents of the array should never change.
 * The array should contain the name of each block that is allowed.
 * In columns block, the only block we allow is 'core/column'.
 *
 * @constant
 * @type {string[]}
 */
const ALLOWED_BLOCKS = ["templazee/tabs"];
const TEMPLATE = [["templazee/tabs"], ["templazee/tabs"]];

function SliderEditContainer({
  attributes,
  setAttributes,
  updateColumns,
  clientId,
}) {

  const {
    tabID,
    tabCount,
    tabLabel1,
    tabLabel2,
    tabLabel3,
    tabLabel4,
    tabLabel5
  } = attributes;
 

  
  const { count } = useSelect(
    (select) => {
      return {
        count: select(blockEditorStore).getBlockCount(clientId),
      };
    },
    [clientId]
    );
  
    setAttributes( { tabCount : count } )
    

  var mainWrapperClassname = `templazee-tab}`;

  const blockProps = useBlockProps({
    className: mainWrapperClassname,
  });
  
  const innerBlocksProps = useInnerBlocksProps(blockProps, {
    allowedBlocks: ALLOWED_BLOCKS,
    orientation: "horizontal",
    renderAppender: false,
    template: TEMPLATE,
  });

  const tabLabels = [];
  var tabTarget = '';
  for (let i = 1; i <= count; i++) {
    tabLabels.push( 'tabLabel'+i );
    tabTarget = tabTarget+attributes['tabLabel'+i]
  }
  setAttributes( { tabID : tabTarget.replaceAll(" ", "_") } )
  const handleTab = event => {

    $(event.target).parent().find( 'li' ).removeClass( 'active' );
    $(event.target).addClass( 'active' );
    var tabId = $(event.target).attr( 'data-id' );
    $(event.target).parent().parent().parent().find( '#templazee-tab-content .wp-block-templazee-tabs' ).attr('data-tab-active','inactive');

    $(event.target).parent().parent().parent().find( '#templazee-tab-content .wp-block-templazee-tabs:nth-child('+tabId+')' ).attr('data-tab-active','active');
}

  return (
    <>
      <InspectorControls >
        <PanelBody>
          <RangeControl
            label={__("Number of Tabs")}
            value={count}
            onChange={(value) => updateColumns(count, value)}
            min={2}
            max={Math.max(5, count)}
          />
        </PanelBody>
      </InspectorControls>
      <div {...blockProps}>
          <div id="templazee-tab" data-uniquie={tabID}>
              <div id="templazee-tab-item">
                  <ul>
                      { tabLabels.map((tabLabel, index )=> (
                        <li  className="inactive" onClick={handleTab} data-id={index+1} data-uniquie={tabID}>
                          
                          <RichText
                            tagName="span" 
                            value={ attributes[tabLabel] } 
                            onChange={ ( content ) => setAttributes( { [tabLabel] : content } ) } 
                            placeholder={ __( 'Tab LAbel' ) }
                          />
                        </li>
                      ))}
                      
                  </ul>
              </div>
              <div id="templazee-tab-content" data-uniquie={tabID}>    
              { innerBlocksProps.children }
              </div>
          </div>
        
      </div>
    </>
  );
}

const SliderEditContainerWrapper = withDispatch(
  (dispatch, ownProps, registry) => ({
    /**
     * Update all child Column blocks with a new vertical alignment setting
     * based on whatever alignment is passed in. This allows change to parent
     * to overide anything set on a individual column basis.
     *
     * @param {string} verticalAlignment the vertical alignment setting
     */

    /**
     * Updates the column count, including necessary revisions to child Column
     * blocks to grant required or redistribute available space.
     *
     * @param {number} previousColumns Previous column count.
     * @param {number} newColumns      New column count.
     */

    updateColumns(previousColumns, newColumns) {

     
      const { clientId } = ownProps;
      const { replaceInnerBlocks } = dispatch(blockEditorStore);
      const { getBlocks } = registry.select(blockEditorStore);

      let innerBlocks = getBlocks(clientId);

      // Redistribute available width for existing inner blocks.
      const isAddingColumn = newColumns > previousColumns;

      if (isAddingColumn) {
        innerBlocks = [
          ...innerBlocks,
          ...times(newColumns - previousColumns, () => {
            return createBlock("templazee/tabs");
          }),
        ];
      } else {
        // The removed column will be the last of the inner blocks.
        innerBlocks = dropRight(innerBlocks, previousColumns - newColumns);
      }
      
      replaceInnerBlocks(clientId, innerBlocks);
      
    },
  })
)(SliderEditContainer);

const SliderEdit = (props) => {
  const Component = SliderEditContainerWrapper;

  return <Component {...props} />;
};

export default SliderEdit;
